@font-face {
  font-family: "iconfont";
  src: url(/static/media/iconfont.91a3de7a.eot); /* IE9 */
  src: url(/static/media/iconfont.91a3de7a.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
