.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: '\5FAE\8F6F\96C5\9ED1', 'Microsoft YaHei';
}

.header_wrapper {
  height: 58px;
  border-bottom: 1px solid #f0f0f0;
  position: relative; }
  .header_wrapper .iconfont {
    margin: 0 5px; }
  .header_wrapper .logo {
    height: 58px;
    width: 100px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background: url(data:image/png;base64,);
    background-size: contain; }
  .header_wrapper nav {
    width: 960px;
    height: 100%;
    margin: 0 auto; }
    .header_wrapper nav span {
      line-height: 56px;
      padding: 0 15px;
      font-size: 17px;
      color: #333; }
      .header_wrapper nav span.left {
        float: left; }
      .header_wrapper nav span.right {
        float: right;
        color: #969696; }
      .header_wrapper nav span.active {
        color: #ea6f5a; }
      .header_wrapper nav span.iconfont {
        margin-top: 2px; }
    .header_wrapper nav .search_wrapper {
      position: relative;
      float: left;
      height: 56px; }
      .header_wrapper nav .search_wrapper #navSearch {
        width: 160px;
        height: 38px;
        margin-top: 9px;
        margin-left: 20px;
        padding: 0 35px 0 20px;
        border: none;
        outline: none;
        border-radius: 19px;
        background: #eee;
