          transition: width .3s ease-in-out; }
        .header_wrapper nav .search_wrapper #navSearch::-webkit-input-placeholder {
          color: #999; }
        .header_wrapper nav .search_wrapper #navSearch::-ms-input-placeholder {
          color: #999; }
        .header_wrapper nav .search_wrapper #navSearch::placeholder {
          color: #999; }
      .header_wrapper nav .search_wrapper i.iconfont {
        position: absolute;
        right: 0;
        bottom: 12px;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        border-radius: 50%;
        -webkit-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out; }
        .header_wrapper nav .search_wrapper i.iconfont.focused {
          background: #777;
          color: white;
          -webkit-transition: all .3s ease-in-out;
          transition: all .3s ease-in-out; }
    .header_wrapper nav .search_info {
      position: absolute;
      left: 0;
      top: 56px;
      width: 240px;
      padding: 0 20px;
      box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
      background: #fff; }
      .header_wrapper nav .search_info::after {
        content: '';
        display: block;
        position: absolute;
        top: -20px;
        left: 30px;
        width: 0;
        height: 0;
        border: 13px solid transparent;
        border-bottom: 13px solid #fff; }
      .header_wrapper nav .search_info i.spin {
        position: absolute;
        display: block;
        top: 13px;
        right: 55px;
        cursor: pointer;
        -webkit-transition: all .2s;
        transition: all .2s;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transform-origin: center;
                transform-origin: center; }
      .header_wrapper nav .search_info .search_info_title {
        float: left;
        margin-top: 20px;
        margin-bottom: 15px;
        line-height: 20px;
        font-size: 14px;
        color: #969696; }
      .header_wrapper nav .search_info .search_info_switch {
        float: right;
