        overflow: hidden; }
        .header_wrapper nav .search_info .search_info_contents a {
          font-size: 12px;
          padding: 0 5px;
          line-height: 20px;
          border: 1px solid #ddd;
          color: #777;
          border-radius: 3px;
          display: block;
          float: left;
          margin-right: 10px;
          margin-bottom: 10px;
          cursor: pointer; }
  .header_wrapper div.addition {
    position: absolute;
    right: 0;
    top: 0;
    height: 56px; }
    .header_wrapper div.addition .btn {
      float: right;
      line-height: 38px;
      border-radius: 19px;
      margin-top: 9px;
      margin-right: 20px;
      padding: 0 20px;
